.wrap{
    max-width: 350px;
    width: 80vw;
    height: 90vh;
    margin: 5vh auto;
    padding: 5vh 0px;
    border: 1px solid #eee;
    box-sizing: border-box;
    border-radius: 5px;
}
.params{
    color: #eee;
    font-weight: 900;
    background-color: orange;
    padding: 0.2rem;
    border-radius: 5px;
}
.all_day{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem 0px;
    width: 100%;
}
.day{
    text-align: center;
    margin: 0px 0.5rem 0px 0px;
    font-weight: bold;
}
.circle{
    text-align: center;
    width: 30px;
    height: 30px;
    border-radius: 30px;
    margin: 5px;
    background-color: yellow;
}
.click{
    width: 250px;
    background-color: purple;
    border: none;
    border-radius: 5px;
    padding: 1rem;
    color: #eee;
}
